@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");

* {
   padding: 0;
   margin: 0;
}

.font-josefin {
   font-family: "Josefin Sans", sans-serif;
}

.wrapper {
   display: grid;
   grid-template-columns: 80px 1fr;
   background-color: #333;
   height: 100vh;
}

.logo {
   height: 10%;
}

.nav-link {
   display: flex;
   height: 90%;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   transition: 0.5s;
}

.nav-link > * {
   width: 100%;
   text-align: center;
}

.nav-link > *:hover {
   background-color: #333;
   transition: 0.5s;
}

.main {
   background-color: #333;
   width: 100%;
   color: #ddd;
   font-family: "Josefin Sans", sans-serif;
   display: flex;
   padding: 0px;
   justify-content: center;
   align-items: center;
   position: relative;
}

.box-content {
   position: absolute;
   background-color: #3d3d3d;
   padding: 2rem;
   height: 90.5%;
   box-sizing: border-box;
   width: 95%;
   border-radius: 8px;
}

.active {
   animation-name: animateSlide;
   animation-duration: 1s;
   animation-fill-mode: forwards;
}

@keyframes animateSlide {
   from {
      left: -2000px;
   }
   to {
      left: 32px;
   }
}

@media screen and (max-width: 800px) {
   @keyframes animateSlide {
      from {
         left: -2000px;
      }
      to {
         left: 20px;
      }
   }

   .wrapper {
      grid-template-rows: 1fr 60px;
      grid-template-columns: 1fr;

      grid-template-areas: "main" "nav";
   }
   .nav {
      display: flex;
      justify-content: center;
      grid-area: nav;
      border-radius: 15px 15px 0px 0px;
   }

   .logo {
      position: absolute;
      top: -10px;
   }

   .nav-link {
      display: flex;
      height: 100%;
      justify-content: center;
      flex-direction: row;
   }

   .main {
      grid-area: main;
   }

   .box-content {
      top: 40px;
   }
}

@media screen and (max-width: 600px) {
   @keyframes animateSlide {
      from {
         left: -2000px;
      }
      to {
         left: 10px;
      }
   }

   .box-content {
      top: 50px;
   }
}
